/* 
 @url 
 @data 
 @method 
 @ShowLoading 
 @ShowMsg 
 */
const baseURL = "http://www.yijianlian.com/index.php/api/"; //

export default {
	Fetch: function(opt) {
		const TOKEN = uni.getStorageSync('TOKEN');
		// console.log(TOKEN)
		opt = opt || {};
		opt.debug = opt.debug || false;
		opt.url = opt.url || '';
		opt.data = opt.data || null;
		opt.method = opt.method || 'POST';
		opt.header = opt.header || {
			"X-Requested-With":"XMLHttpRequest",
			'Content-Type': 'application/json;charset=UTF-8',
			'Accept': 'application/json'
		};
		if(TOKEN){
			opt.header.Token=TOKEN
		}
		opt.success = opt.success || function() {};
		opt.fail = opt.fail || function() {
			if (opt.ShowLoading) {
				uni.hideLoading();
			}
			uni.stopPullDownRefresh();
			uni.showToast({
				icon: 'none',
				title: ''
			});
		};
		if (opt.debug) {
			console.log(opt.data)
		}
		
		uni.request({
			url: baseURL + opt.url,
			data: opt.data,
			method: opt.method,
			header: opt.header,
			dataType: 'json',
			success: function(res) {
				if (opt.debug) {
					console.log(res)
				}

				if (res.statusCode === 200) {
					if (res.data.code === 200) {
						opt.success(res.data.data);
					} else if (res.data.code === 300) {
						uni.showModal({
							title: '',
							content: '',
							cancelText: '',
							confirmText: '',
							success: function(ret) {
								uni.removeStorage({
									key: 'TOKEN',
									success: function(res) {
										if (ret.confirm) {
											uni.navigateTo({
												url: '/pages/login/login'
											});

										} else if (ret.cancel) {
											uni.reLaunch({
												url: '/pages/index/index',
											});
										}

									}
								});

							}
						});

					} else {
						if (res.data.msg) {
							uni.showToast({
								title: res.data.msg,
								icon: 'none'
							})
						}
					}

				} else {
					uni.showToast({
						title: '',
						icon: 'none'
					})

				}
			},
			fail: function() {
				opt.fail()
			},
			complete: function() {
				uni.hideLoading();
				uni.stopPullDownRefresh()
			}
		})
	},
	UploadFile: function(opt) {
		const TOKEN = uni.getStorageSync('TOKEN');
		const Lang = uni.getStorageSync('lang');
		opt = opt || {};
		opt.debug = opt.debug || false;
		opt.url = opt.url || '';
		opt.header = opt.header || {
			'Accept': 'application/json',
			'X-Version': 'V1',
			'XX-token': TOKEN,
		};
		opt.filePath = opt.filePath || null; //   
		opt.name = opt.name || null; // key ,  key  
		opt.filePath = opt.filePath || null;
		opt.formData = opt.formData || null;
		opt.success = opt.success || function() {};
		uni.showLoading({
			title: "..."
		})

		uni.uploadFile({
			header: opt.header,
			url: baseURL + opt.url,
			filePath: opt.filePath,
			name: opt.name,
			formData: opt.formData,
			success: function(res) {
				var data = JSON.parse(res.data);
				if (opt.debug) {
					console.log(data)
				}
				if (res.statusCode === 200) {
					uni.hideLoading();
					if (data.code == 200) {
						opt.success(data.data);
					} else if (data.code == 300) {
						uni.showModal({
							title: '',
							content: '',
							cancelText: '',
							confirmText: '',
							success: function(ret) {
								uni.removeStorage({
									key: 'TOKEN',
									success: function(res) {
										if (ret.confirm) {
											uni.navigateTo({
												url: '/pages/login/login'
											});

										} else if (ret.cancel) {
											uni.reLaunch({
												url: '/pages/index/index',
											});
										}

									}
								});

							}
						});
					} else {
						uni.showToast({
							title: data.msg,
							icon: 'none'
						})
					}
				} else {
					uni.showToast({
						title: '',
						icon: 'none'
					})
				}
			},
			fail: function() {
				uni.showToast({
					icon: 'none',
					title: ''
				});
			}

		})

	}
}
