import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
	state: {
		isLogin:uni.getStorageSync("TOKEN"),
		userInfo: null,
	},
	mutations: {
		setLogin(state){
			state.isLogin=uni.getStorageSync("TOKEN")
		},
		setLogout(state){
			state.isLogin=false
		}
	},
	actions: {
		navFilter({},e){
			  let {
			      url
			    } = e.currentTarget.dataset;
			let token=uni.getStorageSync("TOKEN")||true;
			if(token){
				uni.navigateTo({
					url:url
				})
			}else{
				uni.showModal({
					title:"",
					content:'',
					confirmText:'',
					success(res) {
						if(res.confirm){
							uni.navigateTo({
								url:'/pages/login/login'
							})
						}
					}
				})
			}
		}
	}
})

export default store
